.goods {
  width: auto;
  background-color: #1B2D37;
}

.goods__wrapper {
  max-width: 1171px;
  min-width: 320px;
  min-height: 850px;
  margin-left: auto;
  margin-right: auto;
  padding: 0 30px;
  padding-top: 88px;
  background-color: #1B2D37;

  h2 {
    margin: 0;
    padding: 0;
    margin-bottom: 53px;
    font-family: "Roboto Condensed", "Arial", sans-serif;
    font-size: 36px;
    line-height: 51px;
    font-style: normal;
    font-weight: normal;
    color: #FFFFFF;
  }

  ul {
    display: flex;
    flex-wrap: wrap;
    max-width: 1171px;
    min-width: 320px;
    padding: 0;
    margin: 0;
    list-style: none;
  }

  li {
    position: relative;
    flex-grow: 1;
    flex-basis: 220px;
    min-height: 620px;
    padding: 0;
    margin: 0;
    margin-bottom: 30px;
    background-color: #1B2D37;
    box-sizing: border-box;
    overflow: hidden;

    &:nth-child(3n+2) {
      margin-left: 30px;
      margin-right: 30px;
    }

    a {
    display: block;
    position: relative;
    width: 100%;
    height: 100%;
    padding-top: 30px;
    padding-left: 45px;
    padding-right: 44px;
    text-decoration: none;
    box-sizing: border-box;

    &::before {
      content: "";
      position: absolute;
      right: 0;
      bottom: 0;
      z-index: 1;
      width: 63px;
      height: 63px;
      background-color: #00B2FF;
      background-image: url("../img/arrow.svg");
      background-repeat: no-repeat;
      background-position: center;
    }
    }

    h3 {
      position: relative;
      z-index: 1;
      max-width: 165px;
      font-family: "Roboto Condensed", "Arial", sans-serif;
      font-size: 20px;
      line-height: 30px;
      font-style: normal;
      font-weight: normal;
      color: #FFFFFF;
    }

    img {
      position: absolute;
      top: 0;
      left: 0;
      z-index: 0;
      opacity: 0.2;
      width: auto;
      height: auto;
    }
  }
}

